* {
  box-sizing: border-box;
}

body {
  align-items: center;
  justify-items: center;
  display: grid;
  overflow: hidden;
}

.deal-wheel {
  --size: clamp(250px, 80vmin, 700px);
  --lg-hs: 0 3%;
  --lg-stop: 50%;
  --lg: linear-gradient( hsl(var(--lg-hs) 0%) 0 var(--lg-stop), hsl(var(--lg-hs) 20%) var(--lg-stop) 100% );
  grid-gap: calc(var(--size) / 20);
  font-size: calc(var(--size) / 21);
  grid-template-areas: "spinner"
                       "trigger";
  align-items: center;
  display: grid;
  position: relative;
}

.deal-wheel > * {
  grid-area: spinner;
}

.deal-wheel .btn-spin {
  grid-area: trigger;
  justify-self: center;
}

.spinner {
  width: var(--size);
  height: var(--size);
  transform: rotate(calc(var(--rotate, 25) * 1deg) );
  border-radius: 50%;
  grid-template-areas: "spinner";
  align-items: center;
  display: grid;
  position: relative;
}

.spinner * {
  grid-area: spinner;
}

.prize {
  padding: 0 calc(var(--size) / 6) 0 calc(var(--size) / 20);
  width: 50%;
  height: 50%;
  transform-origin: 100%;
  transform: rotate(var(--rotate) );
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  align-items: center;
  display: flex;
}

.ticker {
  left: calc(var(--size) / -15);
  width: calc(var(--size) / 10);
  height: calc(var(--size) / 20);
  background: var(--lg);
  z-index: 1;
  -webkit-clip-path: polygon(20% 0, 100% 50%, 20% 100%, 0% 50%);
  clip-path: polygon(20% 0, 100% 50%, 20% 100%, 0% 50%);
  transform-origin: 0;
  position: relative;
}

.btn-spin {
  color: #fff;
  font-size: inherit;
  cursor: pointer;
  background: #000;
  border: none;
  border-radius: .5rem;
  padding: .9rem 2rem 1rem;
}

.btn-spin:disabled {
  cursor: progress;
  opacity: .25;
}

.is-spinning .spinner {
  transition: transform 8s cubic-bezier(.1, -.01, 0, 1);
}

.is-spinning .ticker {
  animation: tick .7s cubic-bezier(.34, 1.56, .64, 1);
}

@keyframes tick {
  40% {
    transform: rotate(-12deg);
  }
}

.prize.selected .text {
  color: #fff;
  animation: selected .8s;
}

@keyframes selected {
  25% {
    text-shadow: 1vmin 1vmin rgba(0, 0, 0, .1);
    transform: scale(1.25);
  }

  40% {
    text-shadow: 0 0 rgba(0, 0, 0, .2);
    transform: scale(.92);
  }

  60% {
    text-shadow: .5vmin .5vmin rgba(0, 0, 0, .1);
    transform: scale(1.02);
  }

  75% {
    transform: scale(.98);
  }

  85% {
    transform: scale(1);
  }
}

.backdrop.is-hidden {
  display: none;
}

.backdrop {
  z-index: 99;
  width: 100vw;
  height: 100vh;
  background-color: rgba(178, 112, 65, .565);
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  width: 400px;
  height: 500px;
  background-color: #2f8e68;
  border-radius: 4px;
  padding: 72px 24px 24px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 1px 0 2px rgba(0, 0, 0, .2), 3px 0 1px rgba(0, 0, 0, .12), 1px 0 1px rgba(0, 0, 0, .14);
}

.modal-close-btn {
  width: 24px;
  height: 24px;
  cursor: pointer;
  background-color: #e7e9fc;
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), border .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 24px;
  right: 24px;
}

.modal-close-btn:hover, .modal-close-btn:focus {
  background-color: #404bbf;
  border: none;
}

.modal-close-icon {
  fill: var#2e2f42;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.modal-close-btn:hover .modal-close-icon, .modal-close-btn:focus .modal-close-icon {
  fill: #fff;
}

.form-search-by-type {
  margin-top: 50px;
}

